  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Parkinsans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  display: flex;
  min-height: 100vh;
  background-color: #EEEEEE;
}

p {
  text-transform: capitalize;
}

strong {
  color: #E84545;
  font-size: 18px;
}

span {
  font-size: 14px;

}

sub {
  font-size: 14px;
  font-style: italic;
  color: #575757;
  cursor: pointer;
}

sub:hover {
  color: #333;
}

.taskCompleted {
  text-decoration: line-through;
}

button {
  padding: 10px 15px;
  background-color: transparent;
  border: none;
  color: #00ADB5;
  cursor: pointer;
}

button:hover {
  color: #028e96;
}

/* Side Navigation Styles */
.side-nav {
  width: 350px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #222831;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.side-nav-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  color: #EEEEEE;
  /* border-radius: 10em; */
}

.side-nav img {
  width: 80px;
}

.side-nav-btn {
  padding: 15px;
  color: #EEEEEE;
  background-color: #222831;
  border: none;
  text-align: left;
  font-size: 14px;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.3s;

}

.side-nav-footer {
  text-align: right;
  margin: 2em;
}

.side-nav-footer img {
  width: 30px;
  cursor: pointer;
}

.side-nav-btn i {
  margin-right: 10px;
  font-size: 1.5em;
  color: #00ADB5;
}

.side-nav-btn:hover {
  background-color: #575757;
}
#project-popupBtn{
  padding: 10px 0px;
  background-color: transparent;
  font-size: 1em;
}
#project-popupBtn:hover{
color: #f44336;
}
#project-popupBtn span{
  color: #f44336;
  font-size: 1.3em;
  margin-right: 14px;
  font-weight: 800;
  
}
#project-title i{
  margin-right: 1em;
  color: #f44336;
}
#deleteProjectBtn{
  color: #f44336;
}
/*  */
.project-popup-container {
  display: none;
}

.project-buttons {
  text-align: right;
}

.project-buttons button {
  color: #f44336;
  padding: 8px;
  border-radius: 2em;
  font-size: 1.2em;
}

.project-buttons button:hover {
  color: #f85a5a;
}

.project-lists {
  display: flex;
  margin: 20px 0;
  flex-direction: column;
}

.project-list {
  display: flex;
  justify-content: space-between;

}

#projectNav {
  color: #f4f4f4;
  padding: 15px;
}

#project-title {
  cursor: pointer;
}

#project-title:hover {
  color: #9b9b9b;
}

/* Task List Styles */
ul {
  list-style-type: none;
}

li {
  background-color: white;
  padding: 15px;
  margin: 5px 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* MAIN CONTENT */
.main-content {
  margin-left: 350px;
  padding: 20px;
  width: 100%;
}

.main-content-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.main-img-container {
  display: flex;
  justify-content: center;
  align-items: center;

}

.header-logo-text {
  display: flex;
  align-items: center;
  margin: 0 1em;
  flex-direction: column;
}

.main-content-header-content img {
  width: 4em;
  background-color: #00ADB5;
  border-radius: 50%;
  padding: 10px;
  margin: 1em;
}

h1 {
  margin-bottom: 20px;
}

.button-task-container {
  text-align: right;
}

#addTaskBtn {
  background-color: #00ADB5;
  color: #EEEEEE;
  border-radius: 2em;
  margin: 1em 0;
}

#addTaskBtn:hover {
  background-color: #028e96;

}

/* Modal Styles */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Form Styles */
input[type="text"],
textarea,
input[type="date"] {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;

}

#title, 
#dueDate{
  width: 100%;
  padding: 15px 10px;
}
textarea {
  resize: none;
  height: 12em;
}

.form-buttons {
  text-align: right;
}

.form-buttons button {
  border-radius: 2em;
}

#taskBtn {
  background-color: #00ADB5;
  color: #EEEEEE;
}

#taskBtn:hover {
  background-color: #028e96;
}

#closeModalBtn:hover {
  background-color: #b62b2b;
}

/* PROJECT F*/
#projectInput {
  width: 100%;
  border: #f44336 solid 1px;
  background-color: transparent;
  padding: 1em;
  color: #EEEEEE;
}

#projectInput::placeholder {
  color: #EEEEEE;
}

/* Close Modal Button */
button[type="button"] {
  background-color: #f44336;
  color: white;
}

/* message notification */
.message-notif-content {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 2em 0;
  width: 350px;
  display: flex;
  flex-direction: column-reverse;

}

.msg-notif-text {
  padding: 10px 25px;
  color: #EEEEEE;
  margin: 1em 0;
}

.msg-notif-text.added-task-msg {
  background-color: #00ADB5;
}

.msg-notif-text.updated-task-msg {
  background-color: #a7c957;
}

.msg-notif-text.deleted-task-msg {
  background-color: #f44336;
}


